import React from 'react'
import { is721 } from "../helpers/helpers";
import { ERC20ABI } from "../helpers/ABI";
import { ethers } from 'ethers';

function Allowance(props) {

  function setRevokeClick() {
    // set the contract and make an approve transaction with a zero allowance
    const { provider } = props;
    const signer = provider.getSigner(props.account);
    const contract = new ethers.Contract(props.tx.contract, ERC20ABI, signer)
    is721(props.tx.contract, props.tx.allowanceUnEdited).then((result) => {
      if (result) {
        //revoke erc721 by nulling the address
        contract.approve(0, props.tx.allowanceUnEdited).then((receipt) => {
          console.log("revoked: " + JSON.stringify(receipt));
        }).catch((err) => {
          console.log(err)
          console.log("failed: " + JSON.stringify(err))
        })
      } else {
        // revoke erc20 by nulling approval amount
        contract.approve(props.tx.approved, 0).then((receipt) => {
          console.log("revoked: " + JSON.stringify(receipt));
        }).catch((err) => {
          console.log(err)
          console.log("failed: " + JSON.stringify(err))
        })
      }
    });
  }

  return (

    <tr className="bg-white md:hover:bg-gray-100 flex md:table-row flex-row md:flex-row flex-wrap md:flex-no-wrap mb-10 md:mb-0 font-semibold">
      <td className="w-full md:w-auto p-3 text-center border border-b block md:table-cell relative md:static">
        <span className="md:hidden absolute top-0 left-0 bg-pink-600 px-3 py-2 text-xs font-bold uppercase text-white">Contract</span>
        <a href={props.etherscanURL + props.tx.contract}>{props.tx.contractName}</a>
      </td>
      <td className="w-full md:w-auto p-3  border border-b text-center block md:table-cell relative md:static">
        <span className="md:hidden absolute top-0 left-0 bg-pink-600 px-3 py-2 text-xs font-bold uppercase text-white">Approved</span>
        <a href={props.etherscanURL + props.tx.approved}>{props.tx.approvedName}</a>
      </td>
      <td className="w-full md:w-auto p-3  border border-b text-center block md:table-cell relative md:static">
        <span className="md:hidden absolute top-0 left-0 bg-pink-600 px-3 py-2 text-xs font-bold uppercase text-white">Allowance</span>
        {props.tx.allowance}
      </td>
      <td className="w-full md:w-auto p-3  border border-b text-center block md:table-cell relative md:static">
        <span className="md:hidden absolute top-0 left-0 bg-pink-600 px-3 py-2 text-xs font-bold uppercase text-white">Actions</span>
        <button
          className="
            p-2
            font-bold
            text-white
            rounded
            border-2 border-white
          bg-red-500 hover:bg-red-900
            transition
            duration-500
          "
          onClick={() => { setRevokeClick() }}
          name="revoke"
        >
          Revoke
        </button>
      </td>
    </tr>
  )
}

export default Allowance
